import { Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { FormListComponent } from "@fragments";
import { ApiService, SweetAlertService } from "@services";
import { COMMON_FUNCTION, ENDPOINTS, HOLIDAY_TYPE, STATUSES } from "@shared";
import { Holiday } from "app/core/models/holiday.model";
import { DateService } from "app/core/services/date.service";
import Swal from "sweetalert2";

@Component({
  selector: "app-holiday",
  templateUrl: "./holiday.component.html",
  styleUrls: ["./holiday.component.scss"],
})
export class HolidayComponent implements OnInit {
  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<Holiday>;
  displayedColumns: string[] = [
    "holidayName",
    "holidayDate",
    "holidayType",
    "status",
  ];
  statusList = [...STATUSES];
  holidayTypeList = [...HOLIDAY_TYPE];

  holiday = new Holiday();
  holidayList = [];

  holidayForm: FormGroup;

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
    private dateService: DateService
  ) {}

  ngOnInit() {
    this.getList();
    this.setFormValidator();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.holidays).subscribe((res: any) => {
      this.holidayList = res.responseData.data;
      this.dataSource = new MatTableDataSource<Holiday>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  save(): void {
    this.holiday.holidayDate = this.dateService.setDate(
      this.holiday.holidayDate
    );
    this.apiService.save(ENDPOINTS.holidays, this.holiday).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      },
      (err: any) => {
        this.sweetAlertService.error(err);
      },
      () => {
        this.resetFormValidator(), this.getList();
      }
    );
  }

  getHoliday(): void {
    this.apiService
      .findById(ENDPOINTS.holidays, this.formListComponent.idForUpdate)
      .subscribe((res: any) => {
        if (res) {
          this.holiday = res.responseData.data;
          console.log(res);
        }
      });
  }

  update(): void {
    this.holiday.holidayDate = this.dateService.setDate(
      this.holiday.holidayDate
    );
    this.apiService.update(ENDPOINTS.holidays, this.holiday).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      },
      (err: any) => {
        this.sweetAlertService.error(err);
      },
      () => {
        this.resetFormValidator();
        this.getList();
      }
    );
  }

  setFormValidator(): void {
    this.holiday.status = COMMON_FUNCTION.ACTIVE;
    this.holidayForm = this.formBuilder.group({
      holidayName: ["", Validators.required],
      holidayDate: ["", Validators.required],
      holidayType: ["", Validators.required],
      status: [""],
    });
  }

  resetFormValidator(): void {
    this.holiday = new Holiday();
    this.holiday.status = COMMON_FUNCTION.ACTIVE;
    this.holidayForm.markAsUntouched();
    this.holidayForm.markAsPristine();
  }

  disabledWeekEnds = (d: Date | null): boolean => {
    const day = (d || new Date()).getDay();

    if (this.holiday.holidayType === "WEEKEND") {
      return day === 0 || day === 6;
    }
    return day !== 0 && day !== 6;
  };

  delete() {
    Swal.fire({
      text: "Are you sure you want to delete this holiday?",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Confirm",
    }).then((result) => {
      if (result.value) {
        this.apiService
          .deleteById(ENDPOINTS.holidays, this.holiday.id)
          .subscribe(() => {
            this.sweetAlertService.customSuccessMessage(
              "Holiday successfully deleted."
            );
            this.resetFormValidator();
            this.getList();
          });
      }
    });
  }
}
